import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/view/Home.vue";
import Profile from "@/view/Profile.vue";
import Contact from "@/view/Contact.vue";
import Portfolio from "@/view/Portfolio.vue";
import Sidebar from "@/components/Sidebar.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    redirect: "Home",
    name: "Sidebar",
    component: Sidebar,
    children: [
      {
        path: "/Home",
        name: "Home",
        component: Home,
        meta: {
          guest: true,
        },
      },
      {
        path: "/Profile",
        name: "Profile",
        component: Profile,
        meta: {
          guest: true,
        },
      },
      {
        path: "/Contact",
        name: "Contact",
        component: Contact,
        meta: {
          guest: true,
        },
      },
      {
        path: "/Portfolio",
        name: "Portfolio",
        component: Portfolio,
        meta: {
          guest: true,
        },
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});
export default router;
